// - Page Imports
@import './_headerStyles/header';
@import './_bodyStyles/navbar';
@import './_mainStyles/aboutStyles';
@import './_mainStyles/blogStyles';
@import './_mainStyles/meetupStyles';
@import './_mainStyles/leadershipStyles';
@import './_footerStyles/footer.scss';
@import '../../node_modules/sal.js/dist/sal.css';

// - Global Rest

html {
  box-sizing: border-box;
  font-size: 16px;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
  font-family: Arial, Helvetica, sans-serif;
}

ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

// - Global Media queries
@media only screen and (min-width: 0px) and (max-device-width: 610px) {
  section hr {
    width: 30vw !important;
  }
}
